import { Card, CardContent, Typography, makeStyles } from "@material-ui/core";
import React from "react";

const useStyles = makeStyles({
  wrapper: (props) => {
    if (props.type === "confirmed") 
        return { borderLeft: "5px solid #c9302c" };
    else if (props.type === "recovered") 
        return { borderLeft: "5px solid #28a745" };
    else
        return { borderLeft: "5px solid #6e6668" };
  },
  title: {
    fontSize: 18,
    marginBottom: 5,
  },
  count: {
    fontWeight: "bold",
    fontSize: 18,
  },
});

export default function HighlightCard({ title, count, type }) {
  const styles = useStyles({type});

  return (
    <div>
      <Card className={styles.wrapper}>
        <CardContent>
          <Typography component="p" variant="body2" className={styles.title}>
            {title}
          </Typography>
          <Typography component="p" variant="body2" className={styles.title}>
            {count}
          </Typography>
        </CardContent>
      </Card>
    </div>
  );
}
